.navbar-fixed {
    z-index: 1;
}

.page-footer {
    margin-top: 100px;
}

#footer-icons {
    color: white;
    margin-left: 6px;
    margin-right: 6px;
}
.home-nav-item {
    text-decoration: none;
    color: #546e7a;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
} 

.home-nav-item:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.home-nav-icon {
    text-decoration: none;
    color: #546e7a;
} 
