}
#profile-pic {
    margin-right: 20px;
    margin-top: 20px;
    border-radius: 10px;
}
#portfolio {
    height: 100%;
    font-size: 28px;
    margin-bottom: 50px;
}

.portfolio-item {
    margin-top: 30px;
    font-size: 28px;
}

.image-background {
    position: relative;
    height: 180px;
    width: 280px;
    background-color: black;
} 

.nav-images {
  opacity: 1;
  height: 180px;
  width: 280px;
  transition: .5s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.middle {
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}

.portfolio-item:hover .nav-images {
  opacity: 0.3;
}

.portfolio-item:hover .middle {
  opacity: 1;
}

.middle-text {
  color: white;
  font-size: 17px;
  width: 200px;
}
.resume {
    /* margin-bottom: 200px; */
    width: 100%;
    height: 2000px;
}
